
body>div {
  text-align: center;
}
ul {
  padding: 0;
}
ul.sortview {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  min-height: 600px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: relative;
}
ul.sortview li {
  background: #808080;
  margin: 10px 1px;
  width: 40px;
  list-style: none;
}
ul.sortview li.selected {
  background: #f00;
}
ul.sortview li.selection_low {
  background: #00f;
}
ul.sortview div.info {
  position: absolute;
  text-align: left;
}
ul.sorts {
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
ul.sorts li {
  margin: 10px 3px;
}
div.controls input[type='range'] {
  width: 50%;
}


